import {userStore} from "../store/store";

export const useTapPower = () => {
    const { currentRank, currentLevel, isInspirationBoostActive, ranks } = userStore(state => ({
        currentRank: state.currentRank,
        currentLevel: state.currentLevel,
        isInspirationBoostActive: state.isInspirationBoostActive,
        ranks: state.ranks,
    }));

    const baseImpactForce = ranks[currentRank].levels[currentLevel - 1].impactForce + 10;
    return isInspirationBoostActive ? baseImpactForce * 3 : baseImpactForce;
};
