import {create} from 'zustand';
import { Swiper } from 'swiper';

import AcolyteIcon from '../assets/icons/acolyte.svg';
import DeaconIcon from '../assets/icons/deacon.svg';
import PriestIcon from '../assets/icons/priest.svg';
import BishopIcon from '../assets/icons/bishop.svg';
import ArchbishopIcon from '../assets/icons/archbishop.svg';

export interface UserRank {
    name: string;
    levels: UserRankLevel[];
    image: string;
}

interface UserRankLevel {
    impactForce: number;
    maxEnergy: number;
    recovery: number;
}

interface StoreState {
    balance: number;
    energy: number;
    userName: string;
    currentRank: number;
    currentLevel: number;
    ranks: UserRank[];
    upgradeRank: () => void;
    setCurrentRank: (rankIndex: number) => void;
    setCurrentLevel: (level: number) => void;
    increaseBalance: (amount: number) => void;
    swiperInstance: Swiper | null;
    setSwiperInstance: (swiper: Swiper) => void
    restoreEnergy: () => void;
    isInspirationBoostActive: boolean;
    setIsInspirationBoostActive: (flag: boolean) => void

    isEnergyProtectionActive: boolean; // Флаг для активации защиты энергии
    activateEnergyProtection: () => void; // Метод для активации защиты энергии
    deactivateEnergyProtection: () => void; // Метод для деактивации защиты энергии
    changeEnergy: (amount: number) => void; // Метод для изменения энергии
}

const ranks: UserRank[] = [
    {
        name: 'Acolyte',
        levels: [
            { impactForce: 0, maxEnergy: 1000, recovery: 5 },
        ],
        image: AcolyteIcon
    },
    {
        name: 'Deacon',
        levels: [
            { impactForce: 200, maxEnergy: 2000, recovery: 10 },
            { impactForce: 250, maxEnergy: 2500, recovery: 12 },
        ],
        image: DeaconIcon
    },
    {
        name: 'Priest',
        levels: [
            { impactForce: 300, maxEnergy: 3000, recovery: 15 },
            { impactForce: 350, maxEnergy: 3500, recovery: 18 },
            { impactForce: 400, maxEnergy: 4000, recovery: 20 },
        ],
        image: PriestIcon
    },
    {
        name: 'Bishop',
        levels: [
            { impactForce: 450, maxEnergy: 4000, recovery: 20 },
            { impactForce: 500, maxEnergy: 4500, recovery: 22 },
        ],
        image: BishopIcon
    },
    {
        name: 'Archbishop',
        levels: [
            { impactForce: 550, maxEnergy: 5000, recovery: 25 },
        ],
        image: ArchbishopIcon
    },
];

export const userStore = create<StoreState>((set, get) => ({
    balance: 0,
    userName: 'Jugglerez',
    energy: ranks[0].levels[0].maxEnergy, // Начальная энергия равна максимальной энергии первого уровня первого ранга
    isEnergyProtectionActive: false,
    currentRank: 0,
    currentLevel: 1,
    ranks,
    swiperInstance: null,
    isInspirationBoostActive: false,

    activateEnergyProtection: () => {
        set({ isEnergyProtectionActive: true });
        setTimeout(() => {
            get().deactivateEnergyProtection();
        }, 15000); // Деактивация защиты энергии через 15 секунд
    },

    deactivateEnergyProtection: () => {
        set({ isEnergyProtectionActive: false });
    },


    setIsInspirationBoostActive: (flag) => set({ isInspirationBoostActive: flag }),

    setSwiperInstance: (swiper) => set({ swiperInstance: swiper }),

    setCurrentRank: (rankIndex) => set({ currentRank: rankIndex }),

    setCurrentLevel: (level) => set({ currentLevel: level }),

    increaseBalance: (amount) => set((state) => ({
        balance: state.balance + amount,
    })),
    changeEnergy: (amount) => {
        if (get().isEnergyProtectionActive) {
            return; // Не изменяем энергию, если защита активна
        }
        set((state) => ({
            energy: state.energy + amount,
        }));
    },

    restoreEnergy: () => set((state) => {
        const { currentRank, currentLevel, ranks } = state;
        const maxEnergy = ranks[currentRank].levels[currentLevel - 1].maxEnergy;
        return {
            energy: maxEnergy,
        };
    }),

    upgradeRank: () => set((state) => {
        const { currentRank, currentLevel, balance, ranks, swiperInstance } = state;
        const rank = ranks[currentRank];
        const isLastLevel = currentLevel >= rank.levels.length;
        const isLastRank = currentRank >= ranks.length - 1;

        if (isLastRank && isLastLevel) {
            return state; // Do nothing if at the highest rank and level
        }

        const newRank = isLastLevel ? currentRank + 1 : currentRank;
        const newLevel = isLastLevel ? 1 : currentLevel + 1;
        const newBalance = balance - 2000;

        // Slide to the correct rank
        if (swiperInstance) {
            swiperInstance.slideTo(newRank);
        }

        return {
            currentRank: newRank,
            currentLevel: newLevel,
            balance: newBalance,
        };
    }),

}));


