import React, {useEffect, useState} from 'react';
import {userStore} from "../store/store";
import {useMiningStore} from "../store/miningStore";
import {CtaButton} from "../styles/styles";
import MiningCoinIcon from '../assets/images/mining-coin-icon.svg';
import styled from "styled-components";
import CoinIconBlack from "../assets/images/coin-icon-black.svg";
import TimerIcon from "../assets/images/timer-icon.svg";
import ButtonFigure from "../assets/images/buttonFigure.svg";
import Popup from "./Popup";


const MiningButton: React.FC = () => {
    const [isTimeUp, setIsTimeUp] = useState(false);
    const [isPopupVisible, setPopupVisible] = useState(false);

    const increaseBalance = userStore((state) => state.increaseBalance)

    const timeLeft = useMiningStore((state) => state.timeLeft)
    const isMining = useMiningStore((state) => state.isMining)
    const startMining = useMiningStore((state) => state.startMining)
    const resetMining = useMiningStore((state) => state.resetMining)
    const claimReward = useMiningStore((state) => state.claimReward)

    useEffect(() => {
        if (timeLeft === 0) {
            setIsTimeUp(true);
        }
    }, [timeLeft]);


    const formatTime = (time: number) => {
        const hours = Math.floor(time / 3600);
        const minutes = Math.floor((time % 3600) / 60);
        const seconds = time % 60;
        return `${hours.toString().padStart(2, '0')}:${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`;
    };

    const handleClaimReward = () => {
        if (timeLeft <= 0 && isMining) {
            claimReward()
            increaseBalance(10000); // Увеличиваем баланс на 100
            resetMining(); // Сбрасываем состояние майнинга
            setIsTimeUp(false)
            setPopupVisible(true);
        }
    };

    return (
        <CtaButton>
            {isMining ? (
                <ButtonContent>
                    <Wrapper>
                        <Icon src={CoinIconBlack} alt="Coin Icon"/>
                        <ButtonPrice>10000</ButtonPrice>
                    </Wrapper>
                    <CentralIcon src={ButtonFigure} alt=""/>
                    <Wrapper>
                        {isTimeUp ? (
                            <ButtonTitle onClick={handleClaimReward}>
                                Claim
                            </ButtonTitle>

                        ) : (
                            <>
                                <Icon src={TimerIcon} alt="Coin Icon"/>
                                <ButtonTitle>{formatTime(timeLeft)}</ButtonTitle>
                            </>

                        )}
                    </Wrapper>

                </ButtonContent>
            ) : (
                <StartMiningButton onClick={startMining}>
                    <MiningCoin src={MiningCoinIcon} alt="Mining Coin Icon"/>
                    Start Mining
                </StartMiningButton>
            )}
            {isPopupVisible && <Popup onClose={() => setPopupVisible(false)} />}

        </CtaButton>
    );
};

const MiningCoin = styled.img`
  margin-right: 5px;
`;
const StartMiningButton = styled.div`
  display: flex;
  justify-content: center;
`;
const ButtonContent = styled.div`
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  padding: 0 15px;
`;

const Wrapper = styled.div`
  display: flex;
  justify-content: space-between;
  font-size: 14px;
`;
const ButtonPrice = styled.div`
`;
const ButtonTitle = styled.div`
  display: flex;
  justify-content: space-between;
  font-size: 14px;

`;
const Icon = styled.img`
  margin-right: 10px;
`;
const CentralIcon = styled.img`
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  @media (max-height: 750px) {
    height: 4vh;
  }
`;

export default MiningButton;
