import {create} from 'zustand';

interface Reward {
    type: string; // Тип вознаграждения (например, "монеты", "предметы" и т.д.)
    amount: number; // Количество вознаграждения
}

// Типы для состояния
interface MiningState {
    isMining: boolean; // Флаг, показывающий, идет ли майнинг
    timeLeft: number; // Оставшееся время в секундах
    reward: Reward | null; // Текущее вознаграждение
    startMining: () => void; // Функция для запуска майнинга
    updateTimer: () => void; // Функция для обновления таймера
    resetMining: () => void; // Сброс состояния майнинга
    claimReward: () => void; // Функция для получения вознаграждения
}

// Константы
const TIMER_IN_SECONDS = 5;

// Создание zustand store для майнинга
export const useMiningStore = create<MiningState>((set, get) => ({
    isMining: false,
    timeLeft: TIMER_IN_SECONDS,
    reward: null, // Начально нет вознаграждения

    startMining: () => {
        if (!get().isMining) {
            set({ isMining: true, timeLeft: TIMER_IN_SECONDS });

            const timerInterval = setInterval(() => {
                const { timeLeft, updateTimer } = get();
                if (timeLeft > 0) {
                    updateTimer();
                } else {
                    clearInterval(timerInterval);
                    // Устанавливаем вознаграждение, когда время истекает
                    set({ reward: { type: 'exampleReward', amount: 100 } });
                }
            }, 1000);
        }
    },

    updateTimer: () => {
        set((state) => ({ timeLeft: state.timeLeft - 1 }));
    },

    resetMining: () => {
        set({
            isMining: false,
            timeLeft: TIMER_IN_SECONDS,
            reward: null // Сбрасываем вознаграждение
        });
    },

    claimReward: () => {
        const { reward } = get();
        if (reward) {
            // Логика для обработки вознаграждения (например, добавление к балансу)
            console.log(`Claimed reward: ${reward.amount} ${reward.type}`);
            // Сбрасываем вознаграждение после получения
            set({ reward: null, isMining: false });
        } else {
            console.log('No reward to claim');
        }
    },
}));